.Card {
    flex: 1;
    margin: 30px;
    border: 3px solid #000;
    border-radius: 10px;
    overflow: hidden;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    min-width: 300px;
}

.Card .Conteudo {
    flex: 1;
    padding: 15px opx;
    background-color: #fff;
    color: black;
}


.Card .Header {
    padding: 20px opx;
    background-color: #000;
    color:#fff;
}

.Card .Footer {
    padding: 10px opx;
    background-color: #000;
    color:#fff;
}
